.container {
  width: 100%;
  padding: 0 20px;

  .products & {
    padding-top: 120px;
    padding-bottom: 100px;
  }

  @media screen and (min-width: 480px) {
    width: 480px;
    margin: 0 auto;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 34px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding: 0 115px;

    .page-header & {
      position: relative;
    }
  }
}
